import React, { FC, useRef } from 'react';
import useEventListener from '@use-it/event-listener';
import { WithClass } from './utils';

type DivProps = React.DetailedHTMLProps<
  React.HTMLAttributes<HTMLDivElement>,
  HTMLDivElement
>;

export type SectionProps = Q2SectionProps &
  DivProps & {
    /**
     * Serves as the header text. Renders into an h2 element.
     */
    label: React.ReactNode;
  };

export const Section: FC<SectionProps> = ({
  label,
  collapsible = false,
  expanded = false,
  onToggle,
  children,
  ...props
}) => {
  const ref = useRef<HTMLDivElement>(null);

  useEventListener(
    'change',
    event => {
      if (onToggle) {
        // @ts-ignore
        onToggle(event.detail.expanded);
      }
    },
    ref.current
  );

  return (
    <q2-section
      collapsible={collapsible ? true : undefined}
      expanded={expanded ? true : undefined}
      label={label}
      ref={ref}
      {...props}
    >
      {children}
    </q2-section>
  );
};

declare module 'react' {
  namespace JSX {
    interface IntrinsicElements {
      'q2-section': SectionProps & WithClass;
    }
  }
}

type Q2SectionProps = {
  /**
   * Flag for whether or not the section can be collapsed.
   */
  collapsible?: boolean;

  /**
   * Flag for whether or not the section content is expanded. Only applicable for collapsible sections.
   */
  expanded?: boolean;

  onToggle?: (open: boolean) => void;
};
