/** @jsx jsx */
import { Box, Flex, Text, jsx } from 'theme-ui';
import { FC } from 'react';

import numeral from 'numeral';

import { DropdownSelectOption } from '../tecton';

type AccountOptionProps = {
  id: string;
  name: string;
  maskedAccountNumber: string;
  balance?: number;
  display?: string;
};

// TODO: Determine how they're handling currency
export const AccountOption: FC<AccountOptionProps> = ({
  id,
  name,
  maskedAccountNumber,
  balance,
  display = `${name} ${maskedAccountNumber}`,
}) => {
  return (
    <DropdownSelectOption value={id} display={display}>
      <Flex sx={{ alignItems: 'center' }}>
        <Box sx={{ flex: 1 }}>
          <Text sx={{ fontWeight: 'bold' }}>{name}</Text>
          <Text sx={{ color: 'muted' }}>{maskedAccountNumber}</Text>
        </Box>
        {balance ? (
          <Text sx={{ fontWeight: 'semibold' }}>
            {numeral(balance).format('($0,0.00)')}
          </Text>
        ) : null}
      </Flex>
    </DropdownSelectOption>
  );
};
