import React from 'react';
import { CacheProvider } from '@emotion/core';
import createCache from '@emotion/cache';

import { ThemeProvider as ThemeUIProvider } from 'theme-ui';

// @ts-ignore
import extraScopePlugin from 'stylis-plugin-extra-scope';

import defaultTheme, { CustomTheme } from '../src/theme';

const cache = createCache({
  key: 'tecton',
  stylisPlugins: [extraScopePlugin('[data-tecton-module]')],
});

type Props = {
  theme?: Partial<CustomTheme> | ((outerTheme: CustomTheme) => CustomTheme);
  children?: React.ReactNode;
};

const ThemeProvider: React.FC<Props> = ({ theme = defaultTheme, children }) => {
  return (
    <CacheProvider value={cache}>
      <ThemeUIProvider theme={theme}>{children}</ThemeUIProvider>
    </CacheProvider>
  );
};

export default ThemeProvider;
