import React, { FC, useEffect, useRef } from 'react';

import useEventListener from '@use-it/event-listener';
import { WithClass } from './utils';

type InputProps = React.DetailedHTMLProps<
  React.InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>;
type OptionProps = React.DetailedHTMLProps<
  React.OptionHTMLAttributes<HTMLOptionElement>,
  HTMLOptionElement
>;
type DivProps = React.DetailedHTMLProps<
  React.HTMLAttributes<HTMLDivElement>,
  HTMLDivElement
>;

export type DropdownSelectProps = Q2DropdownSelectProps &
  InputProps & {
    onSearchInput?: (query: string) => void;
  };

export type DropdownSelectOptionProps = Q2DropdownSelectOptionProps &
  OptionProps;

export type DropdownOptGroupProps = Q2DropdownOptGroupProps & DivProps;

export const DropdownSelectOption: FC<DropdownSelectOptionProps> = ({
  children,
  display,
  ...props
}) => {
  if (!display && typeof children === 'string') {
    display = children;
  }

  return (
    <q2-option display={display} {...props}>
      {children}
    </q2-option>
  );
};

export const DropdownOptGroup: FC<DropdownOptGroupProps> = props => (
  <q2-optgroup {...props} />
);

export const DropdownSelect: FC<DropdownSelectProps> = ({
  children,
  multiple,
  multilineOptions,
  onSearchInput,
  onChange,
  value,
  ...props
}) => {
  const ref = useRef<any>();

  //Emitted when an option is selected.
  useEventListener(
    'change',
    (event: any) => {
      if (!onChange) {
        return;
      }

      const value = multiple
        ? event.detail.selectedOptions
        : event.detail.value;

      onChange(value);
    },
    ref.current
  );

  // Set the `selectedOptions` attribute on the component based on the value.
  // Setting the property directly on the web component doesn't work for array
  // values.
  useEffect(() => {
    if (value && multiple && ref.current) {
      ref.current.selectedOptions = value as string[];
    }
  }, [value, multiple]);

  useEventListener(
    'input',
    event => {
      //@ts-ignore
      const newQuery: string = event.detail.query;
      if (onSearchInput) {
        onSearchInput(newQuery);
      }
    },
    ref.current
  );

  return (
    <q2-select
      multiline-options={multilineOptions}
      multiple={multiple}
      searchable={Boolean(onSearchInput)}
      ref={ref as any}
      value={multiple ? undefined : value}
      style={{ margin: 0 }}
      {...props}
    >
      {children}
    </q2-select>
  );
};

declare module 'react' {
  namespace JSX {
    interface IntrinsicElements {
      'q2-select': DropdownSelectProps & WithClass;
      'q2-option': DropdownSelectOptionProps & WithClass;
      'q2-optgroup': DropdownOptGroupProps & WithClass;
    }
  }
}

type Q2DropdownSelectProps = {
  label?: string;
  /**
   * The current value for the select and should correspond to the
   * value of a nested option. This property is only relevant for
   * single select implementations. Pre-select options for single
   * selects by defining the value property.
   */
  value?: string;
  /**
   * Each item in this array should correspond to the value of a option.
   * This property is only relevant for multiselect implementations.
   * Pre-select options for multiselects by defining the selectedOptions property.
   */
  selectedOptions?: string[];
  /**
   * Binds to the nested q2-input element. Note that error messages are
   * not available to the user. q2-select uses this property to show an
   * error state and its primary use case is for an unfilled field.
   */
  errors?: string[];
  /**
   * Disables all interaction with the field and leverages the disabled
   * visual style of q2-input.
   */
  disabled?: boolean;
  /**
   * Enables multiselect functionality.
   */
  multiple?: boolean;
  /**
   * Enables search functionality.
   */
  searchable?: boolean;
  /**
   * Enables text wrapping for select options. When false,
   * text truncates and does not wrap.
   */
  multilineOptions?: boolean;
  /**
   * Marks the field as optional by passing this value to the nested q2-input.
   */
  optional?: boolean;
};

type Q2DropdownOptGroupProps = {
  /**
   * Serves as the group's header and it is sticky using position:sticky;
   */
  label: string;
  /**
   * Disables all nested options.
   */
  disabled?: boolean;
};

type Q2DropdownSelectOptionProps = {
  /**
   * Serves as the option's value. Should correspond to the q2-select value.
   */
  value: string;
  /**
   * The text that is presented in the field if selected; if not set,
   * the value will be used.
   */
  display?: string;
  /**
   * disables option
   */
  disabled?: boolean;
};
