import React, { FC } from 'react';

import Icon, { Q2IconProps } from './Icon';
import classNames from 'classnames';
import { WithClass } from './utils';

type ButtonProps = Q2ButtonProps &
  React.DetailedHTMLProps<
    React.ButtonHTMLAttributes<HTMLButtonElement>,
    HTMLButtonElement
  >;

const Button: FC<ButtonProps> = ({
  badge = false,
  block = false,
  disabled = false,
  icon = false,
  className,
  ...props
}) => {
  // Tecton adds a `hydrated` classname when Tecton components are loaded but
  // doesn't consistently reapply the classname on rerender.
  const cls = classNames([className]);

  return (
    <q2-btn
      badge={badge}
      block={block ? true : undefined}
      disabled={disabled}
      icon={icon}
      class={cls}
      {...props}
    />
  );
};

// both button and icon have a 'type'; button also has an 'icon'
export type IconButtonProps = Omit<Q2IconProps, 'type'> &
  Omit<ButtonProps, 'icon'> & { icon: Q2IconProps['type'] };

const iconStrokeColor = (color?: ButtonProps['color']) => {
  return color ? `var(--q2-btn-${color}-font-color)` : undefined;
};

export const IconButton: FC<IconButtonProps> = ({
  type,
  icon,
  color,
  strokeWidth,
  stroke = iconStrokeColor(color),
  strokeSecondary,
  ...props
}) => {
  return (
    <Button type={type} color={color} icon={true} {...props}>
      <Icon
        type={icon}
        strokeWidth={strokeWidth}
        stroke={stroke}
        strokeSecondary={strokeSecondary}
      />
    </Button>
  );
};

export default Button;

declare module 'react' {
  namespace JSX {
    interface IntrinsicElements {
      'q2-btn': ButtonProps & WithClass;
    }
  }
}

type Q2ButtonProps = {
  color?: 'primary' | 'secondary';

  /**
   * Use the block attribute to create buttons that fill the width of their
   * parent element.
   */
  block?: boolean;

  /**
   * Add the badge attribute to implement a badge-style q2-btn. Badge buttons
   * are useful for quickly changing between filtered views, similar to tabs.
   * Badge buttons are used to change between the "All" and "Selected" views
   * within a multiselect q2-select, for example.
   */
  badge?: boolean;

  /**
   * Add the disabled prop to a Button to show the disabled state. Disabled
   * buttons display at 40% opacity and show the not-allowed cursor when
   * hovered.
   */
  disabled?: boolean;

  /**
   * Use the icon flag and a nested icon to create an icon-only Button.
   */
  icon?: boolean;
};
