import React, { FC, useRef } from 'react';

import useEventListener from '@use-it/event-listener';
import { WithClass } from './utils';

type RadioGroupProps = Q2RadioGroupProps & InputProps;

export const RadioGroup: FC<RadioGroupProps> = ({
  children,
  onChange,
  ...props
}) => {
  const ref = useRef();

  useEventListener(
    'change',
    (event: any) => {
      if (onChange) {
        event.stopPropagation();
        onChange({
          ...event,
          // @ts-ignore
          type: 'input',
          target: {
            value: event.detail.value,
            formattedValue: event.detail.formattedValue,
          },
        });
      }
    },
    ref.current
  );

  return (
    <q2-radio-group ref={ref as any} {...props}>
      {children}
    </q2-radio-group>
  );
};

type RadioProps = Q2RadioProps & InputProps;

export const Radio: FC<RadioProps> = ({ children, ...props }) => {
  return <q2-radio {...props}>{children}</q2-radio>;
};

type InputProps = React.DetailedHTMLProps<
  React.InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>;

declare module 'react' {
  namespace JSX {
    interface IntrinsicElements {
      'q2-radio-group': RadioGroupProps & WithClass;
      'q2-radio': RadioProps & WithClass;
    }
  }
}

type Q2RadioGroupProps = {
  /**
   * Text for the fieldset legend describing radio group
   *
   * **Localizable**
   */
  label?: string;

  disabled?: boolean;

  optional?: boolean;

  value?: string;

  name?: string;
};

type Q2RadioProps = {
  /**
   * Text for the fieldset legend describing radio group
   *
   * **Localizable**
   */
  label?: string;

  'aria-label'?: string;

  disabled?: boolean;

  value?: string;
};
