import React, { FC } from 'react';

import styled from '@emotion/styled';
import { system } from 'styled-system';

import { createShouldForwardProp } from '@styled-system/should-forward-prop';
import { ResponsiveStyleValue, WithClass } from './utils';

export type IconProps = Q2IconProps &
  React.DetailedHTMLProps<
    Omit<React.SVGAttributes<SVGElement>, 'stroke' | 'strokeWidth'>,
    SVGElement
  >;

const shouldForwardProp = createShouldForwardProp([
  'strokeWidth',
  'stroke',
  'strokeSecondary',
]);

const Q2Icon: FC<IconProps> = ({ className, ...props }) => {
  return <q2-icon class={className} {...props} />;
};

const StyledIcon = styled(Q2Icon, {
  shouldForwardProp,
})(
  system({
    // @ts-ignore
    strokeWidth: {
      property: '--t-icon-stroke-width',
    },
    // @ts-ignore
    stroke: {
      property: '--t-icon-stroke-primary',
      scale: 'colors',
    },
    // @ts-ignore
    strokeSecondary: {
      property: '--t-icon-stroke-secondary',
      scale: 'colors',
    },
  })
);

const Icon: FC<Omit<IconProps, 'ref'>> = ({ type, ...props }) => {
  return <StyledIcon type={type} {...props} />;
};

export default Icon;

declare module 'react' {
  namespace JSX {
    interface IntrinsicElements {
      'q2-icon': IconProps & WithClass;
    }
  }
}

