import * as CSS from 'csstype';
import { ObjectOrArray } from 'styled-system';
import { Theme } from 'theme-ui';

const cssVar = (name: string) => `var(--${name})`;

const cssVarList = (
  start: number,
  end: number,
  prefix: string,
  defaultVal = ''
) => {
  let list = [];
  for (let i = 0; i <= end; i++) {
    if (i < start) {
      // Follow the index pattern of the underlying css var. For example,
      // `highlight.1` should map to `t-highlight-1`, so we skip `highlight.0`
      // since the css variables start at 1.
      list.push(defaultVal);
    } else {
      list.push(cssVar(`${prefix}${i}`));
    }
  }
  return list;
};

export type CustomTheme = Theme & {
  variants: ObjectOrArray<CSS.StandardProperties>;
};

const theme: CustomTheme = {
  fonts: {
    body: 'system-ui, sans-serif',
    heading: '"Avenir Next", sans-serif',
    monospace: 'Menlo, monospace',
  },
  colors: {
    text: cssVar('t-font-color'),
    muted: cssVar('t-a11y-gray-color'),
    background: cssVar('t-content-bg'),
    link: cssVar('t-link-color'),
    linkHover: cssVar('t-link-hover-color'),
    primary: cssVar('t-primary'),
    primaryLight: cssVarList(1, 2, 't-primary-l'),
    primaryDark: cssVarList(1, 2, 't-primary-d'),
    secondary: cssVar('t-secondary'),
    secondaryLight: cssVarList(1, 2, 't-secondary-l'),
    secondaryDark: cssVarList(1, 2, 't-secondary-d'),
    black: cssVar('app-black'),
    white: cssVar('app-white'),
    gray: cssVar('app-gray'),
    grayLight: cssVarList(1, 3, 'app-gray-l'),
    grayDark: cssVarList(1, 3, 'app-gray-d'),
    highlight: cssVarList(1, 15, 't-highlight-'),
    stoplight: {
      info: cssVar('const-stoplight-info'),
      success: cssVar('const-stoplight-success'),
      warning: cssVar('const-stoplight-warning'),
      alert: cssVar('const-stoplight-alert'),
    },
  },
  // https://cdn1.onlineaccess1.com/cdn/base/tecton/v0.39.1/Documentation/index.html#/ui/grid-layout
  breakpoints: ['480px', '768px', '992px', '1200px'],
  space: cssVarList(0, 6, 'app-scale-'),
  radii: cssVarList(1, 2, 'app-border-radius-', '0'),
  shadows: cssVarList(1, 2, 'app-shadow-'),
  variants: {
    primary: {
      bg: 'primary',
      color: 'white',
    },
    secondary: {
      bg: 'secondary',
      color: 'text',
    },
  },
};

export default theme;
